<template>
  <div class="adChModule">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="line"></div>
        <div class="iframeBox">
            <iframe ref="iframe" src="#/adChModuleExample"></iframe>
        </div>
    </div>
    <div class="cy_center">
      <h2 class="cy_h2">adChModule 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
	import adChModule from "cy_static/adChModule"; 
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;div id="test"&gt;内容显示区&lt;/div&gt;
&lt;script type="text/javascript"&gt;
    import adChModule from "cy_static/adChModule";
    adChModule("#test", {
        "keyId" : "Mall_Welfare_adverts",
        "column" : "47.4",
        "unLazyNum" : "3",
        "rowSpace" : "1",
        "colSpace" : "1",
        "paddingTop" : "10",
        "paddingBottom" : "10",
        "paddingLeft" : "0",
        "paddingRight" : "0",
        "moduleSpm" : "",
        "backgroundColor" : "#f0f0f0"
    });
&lt;/script&gt;
</xmp >

      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">构造方法</h2>				
            <p><a>adChModule</a>(dom : selector|String, params : Object = {}) : <a>Void</a></p>
            <p>调用广告推荐位接口，生成左边一列右边2列格式的效果</p>
            <table class="table-props">
            <tr>
                <th colspan="5">params 参数</th></tr>
            <tr>
                <th style="width:20%">参数名</th>
                <th style="width:20%">类型</th>
                <th style="width:20%">默认值</th>
                <th style="width:30%">描述</th>
                <th style="width:10%" class="text-c">必需</th>
            </tr>
            <tr>
                <td>type</td>
                <td class="atn">String</td>
                <td>布局类正倒品型</td>
                <td>asc(asc、desc)</td>
                <td class="text-c">N</td>
            </tr>
            <tr>
                <td>keyId</td>
                <td class="atn">String</td>
                <td>广告推荐位</td>
                <td>""</td>
                <td class="text-c">Y</td>
            </tr>
            <tr>
                <td>column</td>
                <td class="atn">String</td>
                <td>左边图%大小</td>
                <td>40</td>
                <td class="text-c">Y</td>
            </tr>
            <tr>
                <td>rowSpace</td>
                <td class="atn">String</td>
                <td>模块行距离</td>
                <td>5</td>
                <td class="text-c">Y</td>
            </tr>
            <tr>
                <td>colSpace</td>
                <td class="atn">String</td>
                <td>模块列距离</td>
                <td>5</td>
                <td class="text-c">Y</td>
            </tr>
            <tr>
                <td>paddingTop</td>
                <td class="atn">String</td>
                <td>模块上边距</td>
                <td>10</td>
                <td class="text-c">Y</td>
            </tr>
            <tr>
                <td>paddingBottom</td>
                <td class="atn">String</td>
                <td>模块下边距</td>
                <td>10</td>
                <td class="text-c">Y</td>
            </tr>
            <tr>
                <td>paddingLeft</td>
                <td class="atn">String</td>
                <td>模块左边距</td>
                <td>10</td>
                <td class="text-c">Y</td>
            </tr>
            <tr>
                <td>paddingRight</td>
                <td class="atn">String</td>
                <td>模块右边距</td>
                <td>10</td>
                <td class="text-c">Y</td>
            </tr>
            <tr>
                <td>moduleSpm</td>
                <td class="atn">String</td>
                <td>模块SPM编码</td>
                <td>""</td>
                <td class="text-c">N</td>
            </tr>
            <tr>
                <td>moduleName</td>
                <td class="atn">String</td>
                <td>模块名称</td>
                <td>""</td>
                <td class="text-c">N</td>
            </tr>
            <tr>
                <td>backgroundColor</td>
                <td class="atn">String</td>
                <td>模块背景色</td>
                <td>#ffffff</td>
                <td class="text-c">N</td>
            </tr>
            <tr>
                <td>data</td>
                <td class="atn">Object||Array</td>
                <td>本地数据[]</td>
                <td>""</td>
                <td class="text-c">N</td>
            </tr>
            <tr>
                <td>onBefore</td>
                <td class="atn">Function</td>
                <td>渲染前处理</td>
                <td>function(data, params, dom) {}</td>
                <td class="text-c">N</td>
            </tr>
            <tr>
                <td>onAfter</td>
                <td class="atn">Function</td>
                <td>渲染后处理</td>
                <td>function(data, params, dom) {}</td>
                <td class="text-c">N</td>
            </tr>
            </table> 
      </div>




    </div> 
  </div>
</template>

<script>
import {trim} from "../../../assets/js/codeStyle";
export default {
  name: 'adChModule',
  data () {
    return {
      msg: 'adChModule',
      cmOptions:{
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true

      },
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      },
      cmOptions2:{
            readOnly:"true",
            theme : "mdn-like",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },
  mounted(){
    var m = this;  
    ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
        var dom, valDom, index = 0, getDom = function() {
            index++;
            dom = m.$refs[element + index];
            valDom = m.$refs[element + "Val" + index];
        };
        getDom();
        while (dom) {
            dom.codemirror.setValue(trim(valDom.innerHTML));
            getDom();
        }
    });
  }, 
  methods:{
    runcode(){
      var m = this, frame = m.$refs.iframe;
      frame.src = "?a=" + Math.random() + "#/runcode"
      frame.onload = function() {
          frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
      }

    }
  }
}
</script>    
<style scoped>
  .cy_example .iframeBox{position:absolute;top:48px;width:100%;bottom:0}
</style>


